(function() {
  'use strict';

  angular
    .module('innovaccerTest')
    .directive('malarkeyText', malarkeyText);

  /** @ngInject */
  function malarkeyText(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log) {
      var vm = this;

      vm.contributors = [];
    }

  }

})();

angular
  .module('innovaccerTest')
  .filter('searchFor', function(){

	// All filters must return a function. The first parameter
	// is the data that is to be filtered, and the second is an
	// argument that may be passed with a colon (searchFor:searchString)

	return function(arr, searchString){

		if(!searchString){
			return arr;
		}

		var result = [];

		searchString = searchString.toLowerCase();

		// Using the forEach helper method to loop through the array
		angular.forEach(arr, function(item){

			if(item.title.toLowerCase().indexOf(searchString) !== -1){
				result.push(item);
			}

		});

		return result;
	};

});

angular
  .module('innovaccerTest')
  .factory('dataservice', dataservice);

dataservice.$inject = ['$http', '$log'];

function dataservice($http, $log) {
    return {
        getData: getData
    };

    function getData() {
        return $http.get('https://webhose.io/search?token=644d9a6d-6af4-43b8-ae1e-aa6feaebe1ed&format=json&q=language%3A(english)%20thread.country%3AIN&ts=1466143285922')
            .then(getDataComplete)
            .catch(getDataFailed);

        function getDataComplete(response) {
            return response.data;
        }

        function getDataFailed(error) {
            $log.error('XHR Failed for getData.' + error.data);
        }
    }
}

(function() {
  'use strict';

  angular
    .module('innovaccerTest')
    .controller('NewsController', NewsController);

  /** @ngInject */

  NewsController.$inject = ['dataservice', '$log'];
  function NewsController(dataservice, $log) {
    var vm = this;
    vm.newsRenderedData = [];
    vm.turnOffSpinner = false;

    activate();

    function activate() {
        return getData().then(function() {
            $log.info('Activated newDataView View');
        });
    }

    function getData() {
        return dataservice.getData()
            .then(function(data) {
                vm.newsRenderedData = data;
                vm.turnOffSpinner = true;
                return vm.newsRenderedData;
            });
    }
  }
})();

